<script>
import anime from "../../../modules/anime";

let vm = {
  name: "Logo",
  data() {
    return {};
  },
  beforeMount() {
  },
  mounted() {
  },
  destroyed() {
  },
  methods: {
    to_home() {
      let that = this;
      if (that.$parent.is_show_menu) {
        that.$parent.click_arrow();
        setTimeout(function () {
          that.$router.push("/");
        }, 1167);
      } else {
        that.$router.push("/")
      }
    },
    change_color(signch_ck, e, dur) {
      new anime({
        targets: "path.logo-1",
        fill: signch_ck,
        duration: dur,
        easing: "linear",
      });

      new anime({
        targets: "path.logo-2",
        fill: e,
        duration: dur,
        easing: "linear",
      });
    },
  },
};

export default vm;
</script>



