<template>
  <div class="footer_box" id="footer_box">
    <div class="footer_grid">
      <div class="collaborate inline_grid">
        <div class="part_name">
          {{ global.lang_pack.want_to_collaborate }}
        </div>
        <div class="part_intro">
          {{ global.lang_pack.work_with_us }}
        </div>
        <a href="mailto:info@signcheckhk.com" class="email">
          info@signcheckhk.com
        </a>
        <div class="separator"></div>
      </div>

      <div class="say_hi inline_grid">
        <div class="part_name">
          {{ global.lang_pack.want_to_say_hi }}
        </div>
        <div class="part_intro">
          {{ global.lang_pack.general_inquiries }}
        </div>
        <a href="mailto:maxchan@signcheckhk.com" class="email">
          maxchan@signcheckhk.com
        </a>
        <div class="separator"></div>
      </div>

      <div class="join_us inline_grid">
        <div class="part_name">
          {{ global.lang_pack.want_to_join_us }}
        </div>
        <div class="part_intro">
          {{ global.lang_pack.become_a_signcheckteer }}
        </div>
        <a href="mailto:christylam@signcheckhk.com" class="email">
          christylam@signcheckhk.com
        </a>
        <div class="separator"></div>
      </div>

      <div class="learn inline_grid">
        <div class="part_name">
          {{ global.lang_pack.want_to_learn }}
        </div>
        <div class="part_intro">
          {{ global.lang_pack.become_an_intern }}
        </div>
        <a href="mailto:christylam@signcheckhk.com" class="email">
          christylam@signcheckhk.com
        </a>
        <div class="separator"></div>
      </div>

      <div class="hongkong inline_grid">
        <div class="part_name">
          {{ global.lang_pack.view_on_maps }}
        </div>
        <div class="part_intro">
          {{ global.lang_pack.hongkong }}
        </div>
        <div class="footer_address">{{ global.lang_pack.hongkong_address_footer }}
        </div>
      </div>
      <div class="shenzhen inline_grid">
        <div style="height: 20rem"></div>
        <div class="part_intro">
          {{ global.lang_pack.shenzhen }}
        </div>
        <div class="footer_address">{{ global.lang_pack.hongkong_address_footer }}
        </div>
      </div>
    </div>

    <div class="bottom">
      <div class="copyright"> COPYRIGHT-2021 SIGN CHECK DESIGN</div>
      <div class="back_top_global"  @click="back_to_top">{{global.lang_pack.top}}</div>
    </div>
  </div>
</template>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div {
  font-family: HelveticaNeueLTProLtEx;
}

div.footer_box {
  margin-top: 100rem;
  position: relative;
  width: 100%;
  background: #ededed;
}

div.footer_grid {
  display: grid;
  grid-template-columns: 640rem 640rem 640rem;
  padding-top: 20rem;
}

div.inline_grid {
  width: 640rem;
  /* height: 200rem; */
  margin-top: 20rem;
}

div.part_name {
  font-size: 14rem;
  color: #898989;
  line-height: 1;
}

div.part_intro {
  margin-top: 30rem;
  font-weight: bolder;
  color: #251714;
  font-size: 14rem;
  line-height: 1;
}

a.email {
  padding-top: 10rem;
  display: block;
  height: 14rem;
  font-weight: bolder;
  font-size: 14rem;
  color: #251714;
  text-decoration: none;
  line-height: 1;
}

div.separator {
  margin-top: 50rem;
  height: 1rem;
  background: #898989;
  width: 540rem;
}

div.footer_address {
  font-size: 14rem;
  color: #898989;
  white-space: pre-line;
}
div.collaborate {
  grid-row: 1;
  grid-column: 2;
}

div.say_hi {
  grid-row: 1;
  grid-column: 3;
}

div.join_us {
  grid-row: 2;
  grid-column: 2;
}

div.learn {
  grid-row: 2;
  grid-column: 3;
}

div.hongkong {
  grid-row: 3;
  grid-column: 2;
}

div.shenzhen {
  grid-row: 3;
  grid-column: 3;
}

div.bottom{
  height: 100rem;
  overflow: hidden;
}

div.copyright{
  font-size: 14rem;
  position: absolute;
  color: #898989;
  left: 50rem;
  bottom: 50rem;
  transform: translate(0,50%);
}

div.back_top_global{
  position: absolute;
  bottom: 0;
  width: 100rem;
  height: 50rem;
  left: 50%;
  border-radius:50rem 50rem 0 0;
  background: #efcd24;
  transform: translate(-50%, 0);
  font-size: 20rem;
  text-align: center;
  line-height: 50rem;
}
</style>


<script>
import global from "../../pages/global";
import anime from "../../../modules/anime";

let vm = {
  name: "Footer",
  data() {
    return {
      global: global,
    };
  },
  beforeMount() {},
  mounted() {
    window.onresize = () => {
      return (() => {
        this.refresh_root_size();
      })();
    };
  },
  destroyed() {},
  methods: {
    refresh_root_size() {
      let static_piexl = 960;
      let width =
        document.documentElement.clientWidth || document.body.clientWidth;
      if (width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize =
        document.documentElement.clientWidth / 1920 + "px";
      if (is_show_menu) {
        this.show_menu();
        this.change_menu_clock_circle();
      }
    },
    back_to_top() {
      let targets = document.body.getElementsByTagName("div")[0]
      console.log(targets.scrollTop/10)
      anime({
       targets: targets,
       scrollTop: [targets.scrollTop , 0],
       easing: 'linear',
       duration: targets.scrollTop/10
      });
    }
  },
};

export default vm;
</script>



