<template>
  <div id="lang_transfer_box" class="lang_transfer_box">
    <div :style="global.lang==='en'?'color: #251714;':''" class="eng" @click="change_lang('en')">Eng</div>
    <div class="separator"></div>
    <div :style="global.lang==='tc'?'color: #251714;':''" class="tc" @click="change_lang('tc')"></div>
    <div class="separator"></div>
    <div :style="global.lang==='cn'?'color: #251714;':''" class="cn" @click="change_lang('cn')"></div>
  </div>
</template>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

div {
  font-family: thin;
}

div.lang_transfer_box {
  position: fixed;
  z-index: 99;
  top: 50rem;
  right: 150rem;
  font-size: 20rem;
  line-height: 1;
  background: transparent;
  transform: translate(50%, 0);
  display: flex;
  flex-direction: row;
}

div.separator {
  width: 1rem;
  height: 20rem;
  background: #251714;
  opacity: 70%;
}

div.eng, div.tc, div.cn {
  color: #898989;
  cursor: pointer;
  padding-left: 5rem;
  padding-right: 5rem;
  padding-bottom: 1rem;
}

div.eng:hover, div.tc:hover, div.cn:hover {
  color: #efcd24;
}

/**/
@media screen and (max-width: 540px) {
  div.lang_transfer_box {
    visibility: hidden;
    z-index: 100;
    top: unset;
    bottom: -30rem;
    right: 375rem;
    font-size: 40rem;
  }

  div.separator {
    height: 40rem;
  }

}
</style>


<script>
import global from "../../pages/global";
import anime from "../../../modules/anime";

let vm = {
  name: "LangTransfer",
  data() {
    return {
      global: global,
    };
  },
  beforeMount() {
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        global.change_root_size()
      })();
    };
  },
  destroyed() {
  },
  methods: {
    change_lang(lang) {
      global.lang = lang
      global.change_lang()
    },

    show_lang_trans() {
      if(document.getElementById("lang_transfer_box") === null)
        return
      document.getElementById("lang_transfer_box").style.visibility = "visible"
      new anime({
        targets: 'div.lang_transfer_box',
        bottom: "30rem",
        duration: 300,
        easing: 'easeInOutSine',
      });
    },

    hide_lang_trans() {
      if(document.getElementById("lang_transfer_box") === null)
        return
      new anime({
        targets: 'div.lang_transfer_box',
        bottom: "-30rem",
        duration: 300,
        easing: 'easeInOutSine',
        complete: function (anim) {
          document.getElementById("lang_transfer_box").style.visibility = "hidden"
        }
      });
    }
  },
};

export default vm;
</script>



